.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #a8bce6;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.center {
  display: block;
  margin-left: auto;
  margin-right: 50%;
  width: 50%;
}

.left {
  display: block;
  text-align: left;

}

.right {
  text-align: right;
}

.justify {

  text-align: justify;
}

.logo {
  padding: 0px 20px 0px 5px;
  max-width: 100px;
  width: 20vp;
}

.responsive {
  height: auto;
}

.card {
  padding: 5px 70px 5px;
}

.footer {
  background-color: rgb(48, 69, 98);
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.m-0 {
  margin-bottom: 5px;
}